import functionReducer from './_functionReducer';

const getSprintsSuccess = (state, action) => (
    Object.assign(
        {},
        state,
        action.sprints.reduce((sprintsMap, sprint) => (sprintsMap[sprint.guid] = sprint) && sprintsMap, {}),
        { keys: action.sprints.map(it => it.guid) }
    ));
const getSprintSuccess = (state, action) => ({
    ...state,
    [action.sprint.guid]: action.sprint
});

const sprintReducer = functionReducer({
    getSprintSuccess,
    getSprintsSuccess
});

export default sprintReducer;



