import axios from 'axios';
import { throwError } from './errorActions';
import { SPRINTS_GET_SUCCESS, SPRINT_GET_SUCCESS } from './constants';

function getSprintsSuccess(sprints) {
    return {
        type: SPRINTS_GET_SUCCESS,
        sprints
    };
}

function getSprintSuccess(sprint) {
    return {
        type: SPRINT_GET_SUCCESS,
        sprint
    };
}

export const getSprints = () =>
    dispatch => axios.get('/api/sprints').then(
        res => dispatch(getSprintsSuccess(res.data)),
        err => dispatch(throwError(err))
    );

export const getSprint = (guid) =>
    dispatch => axios.get(`/api/sprints/${guid}`).then(
        res => dispatch(getSprintSuccess(res.data)),
        err => dispatch(throwError(err))
    );

export default {
    getSprint,
    getSprints
};



